<script>
	import { 
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					jihuabiaoti : false,
					jihuayongshi : false,
					dakacishu : false,
					yundongmubiao : false,
					jihuaxiangqing : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
					touxiang : false,
					jihuashijian : false,
				},
			
				ruleForm: {
					jihuabiaoti: '',
					jihuayongshi: '',
					dakacishu: Number('0'),
					yundongmubiao: '',
					jihuaxiangqing: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
					touxiang: '',
					jihuashijian: '',
				},

				rules: {
					jihuabiaoti: [
						{ required: true, message: '', trigger: 'blur' },
					],
					jihuayongshi: [
					],
					dakacishu: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					yundongmubiao: [
						{ required: true, message: '', trigger: 'blur' },
					],
					jihuaxiangqing: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
					touxiang: [
					],
					jihuashijian: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.jihuashijian = this.getCurDateTime()
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='jihuabiaoti'){
							this.ruleForm.jihuabiaoti = obj[o];
							this.ro.jihuabiaoti = true;
							continue;
						}
						if(o=='jihuayongshi'){
							this.ruleForm.jihuayongshi = obj[o];
							this.ro.jihuayongshi = true;
							continue;
						}
						if(o=='dakacishu'){
							this.ruleForm.dakacishu = obj[o];
							this.ro.dakacishu = true;
							continue;
						}
						if(o=='yundongmubiao'){
							this.ruleForm.yundongmubiao = obj[o];
							this.ro.yundongmubiao = true;
							continue;
						}
						if(o=='jihuaxiangqing'){
							this.ruleForm.jihuaxiangqing = obj[o];
							this.ro.jihuaxiangqing = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
						if(o=='touxiang'){
							this.ruleForm.touxiang = obj[o];
							this.ro.touxiang = true;
							continue;
						}
						if(o=='jihuashijian'){
							this.ruleForm.jihuashijian = obj[o];
							this.ro.jihuashijian = true;
							continue;
						}
					}
					this.ruleForm.dakacishu = Number('0'); 				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(this.$storage.get("role")!="") {
							this.ro.dakacishu = true;
						}
						if(((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0) && this.$storage.get("role")!=""){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao
							this.ro.yonghuzhanghao = true;
						}
						if(((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0) && this.$storage.get("role")!=""){
							this.ruleForm.yonghuxingming = json.yonghuxingming
							this.ro.yonghuxingming = true;
						}
						if(((json.touxiang!=''&&json.touxiang) || json.touxiang==0) && this.$storage.get("role")!=""){
							this.ruleForm.touxiang = json.touxiang
							this.ro.touxiang = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `yundongjihua/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
						this.ruleForm.jihuaxiangqing = this.ruleForm.jihuaxiangqing.replace(reg,'../../../springbootdti6c7gx/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.touxiang!=null) {
						this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `yundongjihua/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.yundongjihuaCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.yundongjihuaCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			touxiangUploadChange(fileUrls) {
				this.ruleForm.touxiang = fileUrls;
			},
		}
	};
</script>
