<template>
	<div>
		<div v-if="time" style="color: #f00;font-weight: bold;width: 100%;line-height: 2;text-align: center;"> {{time}}</div>
		<div class="mapComponents" id="container"></div>
	</div>
</template>

<script>
	import AMapLoader from "@amap/amap-jsapi-loader";
	window._AMapSecurityConfig = {
	  securityJsCode: "d51a3e347ee75f184f0bfc4a443cfe4c",
	};
	export default {
		data() {
			return {
				map: null,
				time: '',
			}
		},
		mounted() {
			// this.initAMap();
		},
		props: ['longitude1','longitude2','latitude1','latitude2'],
		methods: {
			mapClose(){
				console.log(11)
				this.map.destroy();
			},
			initAMap(longitude1,longitude2,latitude1,latitude2) {
				let that = this
				AMapLoader.reset()
				AMapLoader.load({
						key: "f04ebb18061e825c758c2517b235954f", // WebKey load 
						version: "2.0", //  JSAPI  1.4.15
						// plugins: ['AMap.Driving'], // 'AMap.Scale'
					})
					.then((AMap) => {
						this.map = new AMap.Map("container", {
							// id
							// viewMode: "3D", // 3D
							zoom: 14, // 
							center: [longitude1, latitude1], //
						});
						if(longitude1&&longitude2&&latitude1&&latitude2){
							AMap.plugin('AMap.Driving',
								function() {
									var driving = new AMap.Driving({
										map: that.map,
									}); //
									driving.search(
										new AMap.LngLat(longitude1, latitude1),
										new AMap.LngLat(longitude2, latitude2),
										function(status, result) {
											that.time = that.convertSecondsToHMS(result.routes[0].time)
											console.log(status)
											// result   https://lbs.amap.com/api/javascript-api/reference/route-search#m_DrivingResult
											if (status === "complete") {
												console.log("", result);
											} else {
												console.log("" + result);
											}
										}
									);
								}
							);
						}else {
							this.$message.error('')
						}
					})
					.catch((e) => {
						console.log(e);
					});
			},
			convertSecondsToHMS(seconds) {
				var hours = Math.floor(seconds / 3600);
				var minutes = Math.floor((seconds % 3600) / 60);
				var remainingSeconds = seconds % 60;
			  
				return hours + " " + minutes + " " + remainingSeconds + "";
			}
		}
	}
</script>

<style>
	.mapComponents{
		width: 100%;
		height: 500px;
	}
</style>